
.hero{
	height: 80vh;
	width: 100%;
	background-image: url(../../../public/static/hero-background-image.png);
	background-size: cover;
	background-position: center;
}

.content{
	position: absolute;
	top: 60%;
	left:3%;
	transform: translateY(-50%);
}


.content button{
	background-color: black;
	color: white;
	text-decoration: none;
	border: 2px solid transparent;
	font-weight: bold;
	padding: 13px 30px;
	border-radius: 30px;
	transition: .4s; 
}

.content button:hover{
	background-color: transparent;
	border: 2px solid #f9004d;
	cursor: pointer;
}

.content p{
	color: aliceblue;
}




